import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

const gallery = document.querySelector(".gallery");
const loader = document.querySelector(".dual-orbit-spinner");
let lightbox;

export function createGallery(images) {
  const galleryMarkup = images.map(({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) => {
    return `
    <li class="gallery-item">
      <a class="gallery-link" href="${largeImageURL}">
        <img src="${webformatURL}" alt="${tags}" class="gallery-image">
      </a>
      <div class="info">
        <div class="info-item">
          <span class="label">Likes</span>
          <span class="value">${likes}</span>
        </div>
        <div class="info-item">
          <span class="label">Views</span>
          <span class="value">${views}</span>
        </div>
        <div class="info-item">
          <span class="label">Comments</span>
          <span class="value">${comments}</span>
        </div>
        <div class="info-item">
          <span class="label">Downloads</span>
          <span class="value">${downloads}</span>
        </div>
      </div>
    </li>`;
  }).join("");

  gallery.innerHTML = galleryMarkup;

  // ðŸ”„ Ð†Ð½Ñ–Ñ†Ñ–Ð°Ð»Ñ–Ð·Ð°Ñ†Ñ–Ñ Ð°Ð±Ð¾ Ð¾Ð½Ð¾Ð²Ð»ÐµÐ½Ð½Ñ lightbox
  if (!lightbox) {
    lightbox = new SimpleLightbox('.gallery a', {
      captionsData: 'alt',        // Ð²Ð¸ÐºÐ¾Ñ€Ð¸ÑÑ‚Ð¾Ð²ÑƒÑ” Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚ alt Ð´Ð»Ñ Ð¿Ñ–Ð´Ð¿Ð¸ÑÑƒ
      captionDelay: 250,            // Ð·Ð°Ñ‚Ñ€Ð¸Ð¼ÐºÐ° Ð¿ÐµÑ€ÐµÐ´ Ð¿Ð¾ÐºÐ°Ð·Ð¾Ð¼ Ð¿Ñ–Ð´Ð¿Ð¸ÑÑƒ
      captionPosition: 'bottom',    // Ñ€Ð¾Ð·Ñ‚Ð°ÑˆÑƒÐ²Ð°Ð½Ð½Ñ Ð¿Ñ–Ð´Ð¿Ð¸ÑÑƒ
      animationSpeed: 300,          // ÑˆÐ²Ð¸Ð´ÐºÑ–ÑÑ‚ÑŒ Ð²Ñ–Ð´ÐºÑ€Ð¸Ñ‚Ñ‚Ñ/Ð·Ð°ÐºÑ€Ð¸Ñ‚Ñ‚Ñ
      fadeSpeed: 300,               // Ð¿Ð»Ð°Ð²Ð½Ðµ Ð·Ð³Ð°ÑÐ°Ð½Ð½Ñ
      slideSpeed: 300,              // Ð¿Ð»Ð°Ð²Ð½Ð¸Ð¹ Ð¿ÐµÑ€ÐµÑ…Ñ–Ð´ Ð¼Ñ–Ð¶ Ñ„Ð¾Ñ‚Ð¾
      showCounter: true,            // Ð¿Ð¾ÐºÐ°Ð·ÑƒÑ” "1 Ð· 10"
      navText: ['â†', 'â†’'],          // ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ñ– ÑÑ‚Ñ€Ñ–Ð»ÐºÐ¸
      enableKeyboard: true          // ÐºÐµÑ€ÑƒÐ²Ð°Ð½Ð½Ñ Ð· ÐºÐ»Ð°Ð²Ñ–Ð°Ñ‚ÑƒÑ€Ð¸
    });
  } else {
    lightbox.refresh();
  }
}

export function clearGallery() {
  gallery.innerHTML = "";
}

export function showLoader() {
  loader.classList.remove("visually-hidden");
}

export function hideLoader() {
  loader.classList.add("visually-hidden");
}